.books {
  position: relative;
  padding-bottom: 55px;

  &__text {
    margin-top: 0;
    margin-bottom: 30px;

    font-family: $font-arial;
    font-size: 14px;
    font-style: italic;

    @include desktop {
      margin-bottom: 0;
      order: 2;
    }
  }

  &__calendar {
    position: absolute;
    top: 15px;
    left: 20px;
    z-index: 10;
  }

  &__img-wrapper {
    margin-right: -20px;
    margin-bottom: 35px;

    overflow: auto;

    @include desktop {
      margin-bottom: 0;

      order: 1;

      &::after {
        content: '';
        position: absolute;
        bottom: 19px;
        left: 82px;

        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-bottom: 12px solid white;

      }
    }

    .scroll-content {
      margin-bottom: 15px;
    }

    .scrollbar-track-x,
    .scrollbar-thumb-x {
      height: 4px;
    }

    .scrollbar-thumb-x {
      background-color: $color-attention;
    }
  }

  &__name {
    margin-top: 0;
  }

  &__main-wrapper {
    @include desktop {
      display: flex;
      flex-direction: column;

      padding-bottom: 55px;
      border-bottom: 1px solid #dadbdc;
    }
  }

  &__wrapper {
    position: relative;
  }

  &__about {
    padding-top: 50px;
  }
}
