.btn {
  @include transition(opacity);

  box-sizing: border-box;
  width: 100%;
  padding: 13px 20px;

  font-family: $font-consolas;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;

  background-color: $color-attention;
  border: none;
  border-radius: $main-border-radius;

  &:hover {
    cursor: pointer;
    opacity: $opacity-hover;
  }
}
