.slider-controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  width: 75px;

  &__label {
    @include transition(background-color);

    box-sizing: border-box;
    width: 10px;
    height: 10px;

    border-radius: 50%;
    background-color: #cccccc;
  }
}
