.posts {
  padding: 35px 0;

  @include desktop {
    padding: 55px 0;
  }

  &__container {
    overflow: hidden;
  }

  &__title {
    margin-bottom: 25px;

    @include desktop {
      margin-bottom: 30px;
    }
  }

  &__slider-controls {
    margin: 15px auto;

    @include desktop {
      display: none;
    }
  }

  &__list {
    @include desktop {
      transform: translateX(0);
    }
  }
}

#post-slide-1:checked ~ .slider-controls label[ for = 'post-slide-1'],
#post-slide-2:checked ~ .slider-controls label[ for = 'post-slide-2'],
#post-slide-3:checked ~ .slider-controls label[ for = 'post-slide-3'] {
  background-color: $color-attention;
  box-shadow: 0 0 0 2px $color-attention;
}

#post-slide-1:checked ~ .posts__list {
  transform: translateX(0);
}

#post-slide-2:checked ~ .posts__list {
  transform: translateX(-35%);
}

#post-slide-3:checked ~ .posts__list {
  transform: translateX(-69%);
}
