.comment {
  @include desktop {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__textarea {
    margin-bottom: 10px;

    @include desktop {
      height: 100px;
    }
  }

  &__title {
    @include desktop {
      width: 100%;
    }
  }

  &__label {
    @include desktop {
      width: 226px;

      &--placeholder {
        width: 100%;
        margin-bottom: 10px;
      }
    }
  }

  &__select-wrapper,
  &__btn {
    @include desktop {
      width: 226px;
    }
  }
}
