.conclusion {
  padding: 40px 0;

  @include desktop {
    padding: 55px 0;
  }

  &__title {
    margin-bottom: 20px;

    @include desktop {
      margin-bottom: 30px;
    }
  }

  &__text {
    font-family: $font-arial;
    font-size: 14px;
    line-height: 18px;

    @include desktop {
      font-size: 16px;
      line-height: 24px;
    }
  }
}
