.advertising {
  padding: 20px 0;

  font-family: $font-segoe;
  background-color: $color-bg;

  @include desktop {
    padding: 30px 0;
  }

  &__container {
    max-width: 280px;
    margin: 0 auto;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;

    width: 280px;
    margin: 0 auto;

    overflow: hidden;

    @include desktop {
      width: 610px;
    }
  }

  &__img {
    align-self: center;

    @include desktop {
      width: 610px;
      height: 200px;
    }
  }

  &__text {
    margin-top: 0;
    font-size: 14px;
  }
}
