.radio-star {
  @include transition(background);

  display: block;

  width: 24px;
  height: 24px;

  background-image: url('../img/star.svg');

  &:hover {
    cursor: pointer;
  }
}

.radio-star__input:checked ~ .radio-star,
.radio-star:hover ~ .radio-star,
.radio-star:hover {
  background-image: url('../img/star-active.svg');
}
