.placeholder {
  position: relative;
  display: block;

  margin-bottom: 15px;

  &__input {
    padding-top: 21px;
    padding-bottom: 5px;
    margin-bottom: 0;
  }

  &__title {
    @include transition(transform);

    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);


    color: #777777;
    font-family: $font-arial;
    font-size: 14px;
  }

  &__textarea {
    &+ .placeholder__title {
      top: 15%;
    }
  }
}
