.form {
  &__input,
  &__select,
  &__textarea {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
    padding: 13px 20px;

    border: none;
    border-radius: $main-border-radius;

    font-family: $font-arial;
    font-size: 14px;
  }

  &__select {
    &+ .select2-container {
      width: 100% !important;
      margin-bottom: 15px;

      .select2-selection {
        height: 42px;
        padding: 13px 20px;

        border: none;
        font-family: $font-arial;
        font-size: 14px;

        outline: none;

        .select2-selection__arrow {
          b {
            display: none;
          }
          @include transition(transform);

          top: 19px;
          right: 15px;

          width: 11px;
          height: 11px;

          background-image: url('../img/angle-arrow-down.svg');
          background-repeat: no-repeat;
          background-position: center;

        }

        .select2-selection__rendered {
          padding: 0;
          line-height: 1.2;
        }
      }
    }

    &+ .select2-container--open {
      .select2-selection {
        border: 1px solid black;
        border-radius: $main-border-radius !important;
      }

      .select2-selection {
        .select2-selection__arrow {
          background-image: url('../img/angle-arrow-down-active.svg');
          transform: rotate(180deg);
        }
      }
    }
  }

  &__select-wrapper {
    .select2-dropdown {
      top: 10px;

      border: none;
      font-family: $font-arial;
      font-size: 14px;
      box-shadow: 0 0 2px 0 black;
    }

    //hover
    .select2-container--default .select2-results__option--highlighted[aria-selected] {
      position: relative;
      background-color: #ededed;
      color: black;

      &:after {
        content: '';
        position: absolute;

        top: 0;
        left: 0;
        width: 4px;
        height: 100%;

        background-color: $color-attention;
      }
    }

    .select2-results__option {
      padding: 10px;
    }
  }
}
