.page-main {
  padding-top: 25px;

  @include desktop {
    padding-top: 75px;
  }

  &__title {
    position: relative;

    margin-top: 0;
    margin-bottom: 35px;

    font-size: 24px;
    line-height: 48px;
    color: $color-attention;
    text-align: center;

    overflow: hidden;

    @include desktop {
      margin-bottom: 70px;
      font-size: 48px;
    }

    &:before,
    &:after {
      content: '';

      display: inline-block;
      vertical-align: middle;

      box-sizing: border-box;

      width: 100%;
      height: 1px;

      background-color: #dadbdc;
    }

    &:after {
      margin-right: -100%;
      margin-left: 20px;
    }

    &:before {
      margin-left: -100%;
      margin-right: 20px;
    }
  }
}
