.blockquote {
  margin: 0;
  margin-top: 30px;
  padding-left: 55px;
  padding-right: 5px;

  background-image: url('../img/quotation-mark.svg');
  background-repeat: no-repeat;
  background-position: left top;
  background-size: 34px;

  font-family: $font-segoe;

  @include desktop {
    padding-left: 60px;
  }

  &__text {
    margin-bottom: 20px;

    font-size: 14px;
    font-style: italic;
    line-height: 20px;

    @include desktop {
      margin-bottom: 25px;

      font-size: 18px;
      line-height: 24px;
    }
  }

  &__author {
    font-size: 14px;

    @include desktop {
      font-size: 16px;
    }
  }
}
