.post-list {
  @include transition(transform);

  display: flex;
  justify-content: space-between;

  min-width: 900px;

  @include desktop {
    min-width: auto;
    width: 100%;
  }

  &__item {
    width: 280px;

    @include desktop {
      width: 226px;
    }
  }

  &__img {
    margin-bottom: 15px;

    @include desktop {
      width: 226px;
      height: 226px;

      margin-bottom: 10px;
    }
  }
}
